<template>
    <div class="singleDetail">
        <header-btn title="方案信息">
            <div slot="btn">
                <div class="btns" v-if="cptisDiaodu">
                    <Button @click="accept('formValidate')">接受委托</Button>
                    <Button type="primary" @click="unacceptDialog">拒绝委托</Button>
                </div>
                <div class="btns" v-if="!cptisDiaodu">
                    <Button type="primary" @click="sendTrust_m">发送委托</Button>
                </div>
            </div>
        </header-btn>
        <!-- 头部路线箭头 -->
        <line-header :lineHeaderData="cptlineHeaderData"></line-header>
        <div class="tas-box">
            <span>单次物流委托编号：</span>
            <span>{{cptId}}</span>
        </div>

        <div class="public-form-box">
            <div class="public-form-content">
                <Row :gutter="20" class="publc-bottom20">
                    <Col span="18">
                        <Col span="8">
                            <detaiFormItem tit="发货人信息" :titleList="titleList" :valueObj="FH"></detaiFormItem>
                        </Col>
                        <Col span="8">
                            <detaiFormItem tit="收货人信息" :titleList="titleList" :valueObj="SH"></detaiFormItem>
                        </Col>
                        <Col span="8">
                            <detaiFormItem tit="通知人信息" :titleList="titleList" :valueObj="TZ"></detaiFormItem>
                        </Col>
                    </Col>
                    <Col span="6">
                        <detaiFormItem tit="服务参数" :titleList="titleFuWu" :valueObj="formModel"></detaiFormItem>
                    </Col>
                </Row>
                <Row :gutter="20">
                    <Col span="18">
                        <p class="item-form-title">货物信息</p>
                        <div class="public-line"></div>
                        <div class="scroll-box">
                            <cargo-info v-if="timeShow" :arr="entrustCargoRes">
                                <template slot="btns-end">
                                    <span>货物类型：</span>
                                    <span>{{formModel.cargoType}}</span>
                                </template>
                            </cargo-info>
                        </div>
                    </Col>
                    <Col span="6">
                        <detaiFormItem tit="运输要求" :titleList="titleYunShu" :valueObj="formModel"></detaiFormItem>
                    </Col>
                </Row>
            </div>
        </div>
        <!--  拒绝理由弹框 -->
        <adt-my-modal
            :showModal="showItemModal"
            :title="itemTitle"
            @on-visible-change="visibleChange"
            @on-ok="handleItemModalOk"
        >
            <Form
                ref="formValidate"
                :model="remarkForm"
                :rules="remarkFormruleValidate"
                :label-width="80"
            >
                <FormItem label="拒绝理由" prop="remark">
                    <Input
                        v-model="remarkForm.remark"
                        type="textarea"
                        :autosize="{minRows: 2,maxRows: 5}"
                        placeholder="请输入..."
                    ></Input>
                </FormItem>
            </Form>
        </adt-my-modal>
    </div>
</template>

<script>
import {
    apiGetPeriodOnceEntrustDetails,
    apiModifyEntrustStatus,
} from '@/api/logisticsCommission'
import validateRules from '@/libs/validateRules'
import moment from 'moment'
import formCargoInfo from '@/mixins/formCargoInfo'
import CargoInfo from './CargoInfo4'
import lineHeader from './line-header/index'
import headerBtn from '../order/header-btn/index'
import detaiFormItem from './detai-form-item/index'

export default {
    name: 'singleDetail',
    mixins: [formCargoInfo],
    components: {
        CargoInfo,
        lineHeader,
        headerBtn,
        detaiFormItem,
    },
    data() {
        return {
            // 收发通
            titleList: [
                { title: '通知人名称：', name: 'attenName' },
                { title: '代码：', name: 'code' },
                { title: '地址：', name: 'attenAddr' },
                { title: '电话：', name: 'attenTel' },
                { title: '传真：', name: 'attenFax' },
                { title: '邮箱：', name: 'attenEmail' },
            ],
            FH: {},
            SH: {},
            TZ: {},
            // 服务参数
            titleFuWu: [
                { title: '运输类型：', name: 'trspType' },
                { title: '集装箱尺寸：', name: 'contaSize' },
                { title: '集装箱来源：', name: 'containerSource' },
                {},
                {},
                {},
            ],
            // 运输要求
            titleYunShu: [
                { title: '主要运输方式：', name: 'trspMode' },
                { title: '物流类型：', name: 'logisticsType' },
                { title: '提箱类型：', name: 'suitcaseType' },
                { title: '集装箱数量：', name: 'suitcaseQty' },
                {
                    title: '货物适宜温度：',
                    name: 'setTemperatureAt',
                    unit: '°C',
                },
                { title: '出口报关地：', name: 'exPort' },
                { title: '进口报关地：', name: 'imPort' },
                { title: '最迟启运时间：', name: 'lastStartTime' },
                { title: '最低时长要求：', name: 'lowestTimeReq', unit: '天' },
                { title: '最高费用要求：', name: 'highestCostReq', unit: '元' },
                { title: '预计货好时间：', name: 'cargoReadyTime' },
                { title: '最晚到达时间：', name: 'lastArriveTime' },
                { title: '合同编号：', name: 'contractCode' },
                { title: '备注：', name: 'remark' },
            ],
            showItemModal: false,
            itemTitle: '拒绝理由',
            remarkForm: {},
            remarkFormruleValidate: {
                remark: [
                    {
                        required: true,
                        message: '拒绝理由必填',
                        trigger: 'blur',
                    },
                    {
                        type: 'string',
                        min: 2,
                        message: '最小输入两个字符',
                        trigger: 'blur',
                    },
                ],
            },
            // 数据等待
            timeShow: false,
            // 表单
            formModel: {},
            entrustCargoRes: [],
        }
    },
    computed: {
        cptId() {
            let str = this.$route.query.executoryEntrustCode
            return str
        },
        cptexecutoryEntrustCode() {
            let str = this.$route.query.executoryEntrustCode
            return str
        },
        cptlineHeaderData() {
            let arr = JSON.parse(this.$route.query.lineHeaderData)
            return arr
        },
        //零时判断角色的
        cptisDiaodu() {
            let str = this.$route.query.role
            if (str == 'admin') {
                return true
            } else {
                return false
            }
        },
    },
    created() {
        this.getDetail()
    },
    methods: {
        // 弹框里面的取消按钮
        visibleChange(data) {
            this.showItemModal = data
        },
        // 弹框里面的确认按钮
        handleItemModalOk() {
            this.showItemModal = false
            this.unaccept()
        },
        //点击打开拒绝弹框
        unacceptDialog(name) {
            this.showItemModal = true
        },
        //我是接受委托
        accept(name) {
            let data = {
                executoryEntrustCode: this.cptexecutoryEntrustCode, //子委托编号
                entrustExecutoryStatus: '已接受', //子委托状态
            }
            apiModifyEntrustStatus({
                data,
            }).then(({ data }) => {
                this.$Message.success('操作成功')
            })
        },
        unaccept(name) {
            //拒绝接受委托
            let data = {
                executoryEntrustCode: this.cptexecutoryEntrustCode, //子委托编号
                remark: this.remarkForm.remark,
                entrustExecutoryStatus: '已拒绝', //子委托状态
            }
            apiModifyEntrustStatus({
                data,
            }).then(({ data }) => {
                this.$Message.success('操作成功')
            })
        },
        save(name) {
            //我是查看方案详情
            alert('功能开发中')
        },
        getDetail() {
            // 查询详情
            apiGetPeriodOnceEntrustDetails({
                executoryEntrustCode: this.cptId,
            }).then(({ data }) => {
                let {
                    transportationAskRes,
                    entrustAttenResList,
                    entrustCargoRes,
                } = data
                let f = entrustAttenResList.find(
                    (item) => item.linkType == '发货人'
                )
                this.FH = f
                let s = entrustAttenResList.find(
                    (item) => item.linkType == '收货人'
                )
                this.SH = s
                let t = entrustAttenResList.find(
                    (item) => item.linkType == '通知人'
                )
                this.TZ = t
                let obj = {
                    ...transportationAskRes,
                    cargoType: entrustCargoRes[0].cargoType,
                }
                this.formModel = {
                    ...obj,
                    lastStartTime:
                        obj.lastStartTime &&
                        moment(obj.lastStartTime).format('YYYY-MM-DD'),
                    cargoReadyTime:
                        obj.cargoReadyTime &&
                        moment(obj.cargoReadyTime).format('YYYY-MM-DD'),
                    lastArriveTime:
                        obj.lastArriveTime &&
                        moment(obj.lastArriveTime).format('YYYY-MM-DD'),
                }
                this.entrustCargoRes = entrustCargoRes
                this.timeShow = true
            })
        },
    },
}
</script>
<style lang="scss" scoped>
.singleDetail {
    .scroll-box {
        height: 600px;
        overflow-y: auto;
    }
    .public-form-box {
        width: 100%;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 2px 4px 0px rgba(51, 51, 51, 0.1);
        padding: 20px;
        .public-form-content {
            background: rgba(249, 249, 249, 1);
            padding: 0 20px 20px 20px;
            // 这里有一部分在组件里面
            .item-form-title {
                height: 35px;
                font-size: 16px;
                font-weight: bold;
                color: rgba(51, 51, 51, 1);
                line-height: 35px;
            }
        }
    }

    .tas-box {
        padding: 4px 40px;
    }
}
</style>